.input {
  margin-left: 5px;
  background-color: var(--bacInputForm);
  border: 1px solid var(--bacInputForm);
  border-radius: 5px;
  color: var(--fontColor);
  font-size: 16px;
  outline: none;
  height: 20px;
}

input[type="text"] {
}
.textarea {
  margin-left: 5px;
  background-color: var(--bacInputForm);
  border: 1px solid var(--bacInputForm);
  border-radius: 5px;
  width: 100%;
  height: 100px;
  padding: 7px;
  color: var(--fontColor);
  resize: none;
  box-sizing: border-box;
  outline: none;
}
.formSummaryError {
  color: rgb(211, 33, 20);
}
@media (max-width: 360px) {
  .input {
    width: 80%;
  }
}
